.dialogs {
    width: 100%;
    height: 100%;
    display: flex;



}

.messages {
margin: 20px 600px 0 0;

}

.dialog a {
    text-decoration: none;
    color: black;
    padding: 30px;
}
.dialog a:hover {
    display:block;
}

.dialog {
    margin-bottom: 30px;
}


.message {
    background-color: white;
    margin-bottom: 10px;
    width: 150px;
    height: 30px;
    border-radius: 5px;
    padding: 5px;


}

.dialogsItems {
    background-color: white;
    margin: 20px;
    padding: 10px;
    border-radius: 10px;
}
.formDialogs {

    align-self: center;
}
.formDialogs button {
    width: 100px;
    text-transform: uppercase;
    height: 30px;
    color: black;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    margin-top: 20px;
    outline: none;
    user-select: none ;
}
.formDialogs button:hover {
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4);
    color: white;
    transform: translateY(-7px);
}
.formDialogs textarea:hover {
    box-shadow: 0 0 35px #3D5AFE;
}
.formDialogs textarea {
    border-radius:5px;
    height: 200px;
    width: 300px;
}
.buttonCenter {
    display:flex;
    justify-content: center;
}